'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { School, Save, Upload, Loader2 } from 'lucide-react'
import { toast } from 'sonner'

interface SchoolProfile {
  id: string
  name: string
  email: string
  phone: string
  address: string
  logo_url: string
  subscription_plan: string
  subscription_status: string
}

export default function ProfilePage() {
  const [profile, setProfile] = useState<SchoolProfile | null>(null)
  const [formData, setFormData] = useState({ name: '', email: '', phone: '', address: '' })
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')

  useEffect(() => {
    fetchProfile()
  }, [])

  const fetchProfile = async () => {
    try {
      const response = await fetch('/api/dashboard/profile')
      const data = await response.json()

      if (response.ok && data.profile) {
        setProfile(data.profile)
        setFormData({
          name: data.profile.name || '',
          email: data.profile.email || '',
          phone: data.profile.phone || '',
          address: data.profile.address || ''
        })
      }
    } catch (error) {
      console.error('Error fetching profile:', error)
      setError('Failed to load profile')
    } finally {
      setLoading(false)
    }
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setSaving(true)
    setError('')
    setSuccess('')

    try {
      const response = await fetch('/api/dashboard/profile', {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData)
      })

      if (response.ok) {
        setSuccess('Profile updated successfully!')
        toast.success('Profile updated successfully!')
        fetchProfile()
      } else {
        throw new Error('Failed to update profile')
      }
    } catch (err: any) {
      setError(err.message || 'Failed to update profile')
      toast.error('Failed to update profile')
    } finally {
      setSaving(false)
    }
  }

  if (loading) {
    return (
      <div className="space-y-6">
        <div>
          <h2 className="text-3xl font-bold font-heading">School Profile</h2>
          <p className="text-slate-600">Manage your institute's information</p>
        </div>
        <div className="animate-pulse"><div className="h-64 bg-slate-200 rounded-lg"></div></div>
      </div>
    )
  }

  if (!profile) {
    return (
      <div className="space-y-6">
        <div>
          <h2 className="text-3xl font-bold font-heading">School Profile</h2>
          <p className="text-slate-600">Manage your institute's information</p>
        </div>
        <Card>
          <CardContent className="text-center py-12">
            <School className="h-12 w-12 text-slate-400 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-slate-900 mb-2">Profile not found</h3>
            <p className="text-slate-500">Unable to load your school profile.</p>
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-3xl font-bold font-heading">School Profile</h2>
        <p className="text-slate-600">Manage your institute's information</p>
      </div>

      <Card>
        <CardHeader>
          <CardTitle className="flex items-center"><School className="h-5 w-5 mr-2" />Subscription Status</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-between">
            <div>
              <p className="text-lg font-semibold capitalize">{profile.subscription_plan} Plan</p>
              <p className="text-sm text-slate-600">
                Status: <span className={`font-medium ${profile.subscription_status === 'active' ? 'text-green-600' : 'text-red-600'}`}>
                  {profile.subscription_status}
                </span>
              </p>
            </div>
            {profile.subscription_plan === 'basic' && (
              <Button variant="outline" asChild><a href="/pricing">Upgrade to Pro</a></Button>
            )}
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader><CardTitle>Institute Information</CardTitle></CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-6">
            {error && <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-md text-sm">{error}</div>}
            {success && <div className="bg-green-50 border border-green-200 text-green-700 px-4 py-3 rounded-md text-sm">{success}</div>}

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label className="block text-sm font-medium text-slate-700 mb-1">Institute Name *</label>
                <input type="text" name="name" required value={formData.name} onChange={handleInputChange}
                  className="w-full px-3 py-2 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary" />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-1">Email Address</label>
                <input type="email" value={formData.email} disabled
                  className="w-full px-3 py-2 border border-slate-300 rounded-md bg-slate-50 text-slate-500" />
                <p className="text-xs text-slate-500 mt-1">Email cannot be changed</p>
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-1">Phone Number</label>
                <input type="tel" name="phone" value={formData.phone} onChange={handleInputChange}
                  className="w-full px-3 py-2 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary" />
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Institute Address</label>
              <textarea name="address" rows={4} value={formData.address} onChange={handleInputChange}
                className="w-full px-3 py-2 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary" />
            </div>

            <div className="flex justify-end">
              <Button type="submit" disabled={saving}>
                {saving ? (<><Loader2 className="mr-2 h-4 w-4 animate-spin" />Saving...</>) : (<><Save className="mr-2 h-4 w-4" />Save Changes</>)}
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  )
}
